<!--
 * @Descripttion: 
 * @Date: 2020-10-04 23:37:40
-->
<template>
  <div>
    <el-button
      v-show="isShow"
      style="float: right"
      type="text"
      @click="logout()"
    >
      <i class="el-icon-thumb" />
      logout</el-button
    >
    <el-page-header v-show="isShow" @back="goBack" />
    <h1>{{ title }}</h1>
    <el-divider>
      <i class="el-icon-ship" />
    </el-divider>
  </div>
</template>

<script>
import { removeToken } from '@/utils/auth'
export default {
  props: {
    isShow: false,
  },
  data() {
    return {
      //baseUrl: 'http://localhost:5207/api/',
      baseUrl: 'http://47.97.192.85:5207/api/',
      title: '还是偶尔想要伟大',
    }
  },
  methods: {
    goBack() {
      var path = this.$route.path
      if (path === '/publish')
        this.$router.push({ path: this.redirect || '/admin' })
      else this.$router.push({ path: this.redirect || '/' })
    },
    logout() {
      removeToken()
      this.$router.push({ path: this.redirect || '/' })
    },
  },
}
</script>

<style>
h1 {
  font-weight: bolder;
  text-align: center;
}
</style>


