<!--
 * @Descripttion:
 * @Date: 2020-09-30 17:03:56
-->
<template>
  <div class="div_main">
    <headOh isShow="true" />
    <el-row>
      <el-button
        style="float: right; margin-left: 10px"
        circle
        size="medium"
        plain
        type="success"
        icon="el-icon-setting"
        @click="digTitle = true"
      />
      <el-button
        style="float: right"
        circle
        size="medium"
        plain
        type="success"
        icon="el-icon-s-promotion"
        @click="goPublish()"
      />
    </el-row>
    <el-timeline>
      <el-timeline-item
        v-for="(item, index) in data"
        :key="index"
        color="#67C23A"
        size="large"
        type="succuss"
        :timestamp="item.contentDate"
        placement="top"
      >
        <el-card style="border-radius: 7px">
          <el-button
            type="danger"
            plain
            size="mini"
            style="float: right"
            icon="el-icon-delete"
            circle
            @click="deleteItem(index)"
          />
          <p class="content">{{ item.content }}</p>
           <img
            v-if="item.pic"
            :src="ossUrl + item.pic"
            fit="cover"
            style="width: 100%; height: 95%"
          />
        </el-card>
      </el-timeline-item>
      <el-timeline-item
        color="#67C23A"
        size="large"
        type="succuss"
        :timestamp="
          new Date().toLocaleDateString() +
          ' ' +
          new Date().toLocaleTimeString()
        "
        placement="top"
      />
      <p>End</p>
    </el-timeline>
    <footerOh />
    <el-dialog :visible.sync="digTitle" width="90%">
      <el-form
        :rules="rules"
        ref="form"
        :model="form"
        label-width="20px;"
        style="font-size: 20px"
      >
        <el-form-item prop="slogan" label="slogan" style="font-weight: bolder">
          <el-input placeholder="slogan" clearable v-model="form.slogan" />
        </el-form-item>
        <el-form-item>
          <el-button
            style="float: right"
            size="small"
            type="success"
            round
            @click="updateSlogan()"
          >
            <i class="el-icon-attract"
          /></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import headOh from '@/components/headOh.vue'
import footerOh from '@/components/footerOh.vue'
import { removeToken,setSlogan} from '@/utils/auth'
export default {
  name: 'Admin',
  components: {
    headOh,
    footerOh,
  },
  data() {
    return {
      digTitle: false,
      ossUrl: 'https://snkae-1301702299.cos.ap-nanjing.myqcloud.com/',
      //baseUrl: 'http://localhost:5207/api/',
       baseUrl: 'http://47.97.192.85:5207/api/',
      content: '', // 验证连续 内容相同
      count: 0,
      rules: {
        slogan: [{ required: true, message: '请输入内容', trigger: 'blur' }],
      },
      form: {
        slogan: '',
      },
      queryForm: {
        page: 0,
        pageSize: 10,
      },
      data: [],
    }
  },
  created() {
    this.query()
  },
  methods: {
    updateSlogan() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.axios.post(this.baseUrl + 'altSlogan', this.form).then((res) => {
            this.digTitle = false
            setSlogan(this.form.slogan)
          })
        }
      })
    },
    goPublish() {
      this.$router.push({ path: this.redirect || '/publish' })
    },
    query() {
      this.axios.post(this.baseUrl + 'query', this.queryForm).then((res) => {
        this.count = res.data.count
        res.data.data.forEach((element) => {
          this.data.push(element)
        })
      })
    },
    deleteItem(index) {
      this.axios.post(this.baseUrl + 'delete', this.data[index]).then((res) => {
        this.data = []
        this.queryForm.page = 0
        this.$message({
          message: '删除成功',
          type: 'success',
        })
        this.query()
      })
    },
    scroll() {
      let isLoading = false
      window.onscroll = () => {
        // 距离底部200px时加载一次
        let bottomOfWindow =
          document.documentElement.offsetHeight -
            document.documentElement.scrollTop -
            window.innerHeight <=
          10
        if (!bottomOfWindow) isLoading = false
        if (bottomOfWindow && !isLoading) {
          isLoading = true
          this.queryForm.page += 1
          if (this.data.length < this.count) this.query()
        }
      }
    },
  },
  mounted() {
    this.scroll()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
  text-align: center;
}
.div_main {
  margin: 10px 10px 10px 10px;
}
.title {
  margin: 20px 0 20px 0;
}
.content {
  font-size: 15px;
}
.input-text {
  font-size: 16px;
  font-weight: bolder;
  color: #000000;
}
.es-footer {
  font-size: 15px;
  line-height: 21px;
  text-align: center;
  text-shadow: 0 1px 2px #999;
  padding-bottom: 20px;
  width: 100%;
  margin-top: 7.8%;
}
</style>



